.info-container {
    max-width: 300px;
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    align-self: center;
    text-align: left;
}

.info-container h2 {
    align-self: center;
}

.skill-row {
    background-color: white;
    width: 250px;
    height: 20px;
    padding: 10px;
    text-align: left;
    border-radius: 10px;
    box-shadow: 3px 3px 10px darkgray;
    color: white;
    font-weight: 700;
    margin-bottom: 10px;
}

.skill-row .current-skill {
    
    height: 20px;
    padding: 10px;
    position: absolute;
    margin: -10px 0px 0px -10px;
    border-radius: 10px;
    opacity: 0.8;
}

@keyframes react {
  0% {
    width: 0px;
  }
  100% {
    width: 175px;
  }
}

@keyframes react-native {
  0% {
    width: 0px;
  }
  100% {
    width: 225px;
  }
}
@keyframes redux {
  0% {
    width: 0px;
  }
  100% {
    width: 125px;
  }
}

#react.current-skill  {
    animation: 1s ease-out 0s 1 react;
    background-color: #006AB3;
    width: 175px;
    white-space: nowrap;
}

#react-native.current-skill {
    animation: 1s ease-out 0s 1 react-native;
    background-color: orange;
    width: 225px;
    white-space: nowrap;
}

#redux.current-skill {
    animation: 1s ease-out 0s 1 redux;
    background-color: purple;
    width: 125px;
    white-space: nowrap;
}